.App {
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
}












.header_menu {
  display: none;
}

.mobile_menu_wrapper {
  display: block;
  width: 50px;
  background-color: transparent;
  position: relative;
  color: white;
  text-align: center;
  height: 40px;
}

.burger_line {
  position: absolute;
  top: 8px;
  right: 10px;
  width: 35px;
  height: 2px;
  background-color: #fff;
  display: block;
}

#check_menu {
  display: none;
}

.check_menu_label {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 2;
  line-height: 40px;
  text-align: center;
}

.second, .third {
  top: 18px;
}

.fourth {
  top: 28px;
}

#check_menu:checked ~ .mobile_menu {
  max-height: 500px;
  font-size: 16px;
}

#check_menu:checked ~ .first {
  display: none;
}

#check_menu:checked ~ .second {
  transform: rotate(45deg);
}

#check_menu:checked ~ .third {
  transform: rotate(-45deg);
}

#check_menu:checked ~ .fourth {
  display: none;
}

#check_menu:checked ~ .check_menu_label {
  color: red;
}

.mobile_menu_link {
  display: block;
  width: 100%;
  padding: 10px;
  color: white;
  text-decoration: none;
  border-top: 1px solid rgba(255,255,255,.3);
  background-color: #000;
  text-align: center;
  box-sizing: border-box;
  font-size: 3.5vw;
}

.mobile_menu {
  display: block;
  position: absolute;
  width: 150px;
  bottom: 0;
  right: 0;
  transform: translateY(100%);
  max-height: 0;
  font-size: 0;
  overflow: hidden;
  transition: .5s all;
}

.head_menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 50;
  font-size: 1vw;
  text-transform: uppercase;
}

.head_menu::after {
  content: "";
  background-color: #0734ffee;
  opacity: 0.2;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.header_menu_list {
  display: flex;
  padding: 0;
}

.header_menu {
  z-index: 1;
}

.header_menu_list_point {
  list-style-type: none;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: #ffffff;
  margin-left: 0.8rem;
  margin-right: 0.8rem;
}








