@keyframes confetti {
  from {
    -webkit-transform: scale(0.4);
    transform: scale(0.4);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.header_background_petard {
  position: absolute;
  top: 10%;
  left: 0;
  z-index: 1;
  width: 100%;
}

.header_background_confetti {
  position: absolute;
  top: 10%;
  left: 0;
  z-index: 2;
  width: 100%;
  animation-duration: 20s;
  animation-name: confetti;
  /* animation-iteration-count: infinite;
  animation-direction: alternate; */
}

