.main_header {
  font-size: 18px;
  margin-top: 60%;
  z-index: 50;
  color: #ffffff;
  width: 90%;
}

.service_list_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-bottom: 0;
  background-color: #00cafc;
}

.service_list_slider_wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.service_list_slider {
  width: 80%;
  position: relative;
}

.service_list_img_wrapper {
  width: 100%;
  background-color: #e9969e;
}

.service_list_img {
  width: 90%;
}

.service_list_info {
  background-color: #00b8cb;
  width: 20%;
  box-sizing: border-box;
  position: relative;
}

.slider_navigation {
  position: absolute;
  background-color: #00cafc;
  opacity: 0.5;
  display: block;
  border-radius: 30px;
  width: 30px;
  height: 30px;
}

.display_none {
  display: none;
}

.left_arrow::before {
  left: 10px;
  transform: rotate(-45deg);
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 50%;
  border-top: solid 2px;
  border-left: solid 2px;
  border-color: #e9969e;
  transform-origin: 0 0;
}

.right_arrow::after {
  right: -2px;
  transform: rotate(135deg);
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 50%;
  border-top: solid 2px;
  border-left: solid 2px;
  transform-origin: 0 0;
  border-color: #e9969e;
}

.left_arrow {
  top: 45%;
  left: 5px;
}

.right_arrow {
  top: 45%;
  right: 5px;
}







.vertical_text {
  display: block;
  transform: rotate(0.25turn);
  color: #ffffff;
  text-transform: uppercase;
  position: absolute;
}

.service_list_item_name {
  top: 50%;
  left: -25%;
}

.more_info {
  top: 50%;
  right: -40%;
}


.catalog_wrapper {
  width: 100%;
  height: 100vh;
}

.catalog_card {
  display: block;
  background-color: red;
  width: 45%;
  height: 45%;
}









.main_footer {
  display: block;
  height: 50vh;
  width: 100%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);
  background-color: #3991e4;
}





